/* blue rgb(81, 224, 224); */
/* red rgb(221, 63, 63) */
.logo {
  display: inline-block;
  margin-top: 10px;
  margin-bottom: 40px;
  width: 100%;
  text-align: center;
}
.logo:hover .cls1{
  fill: rgb(221, 63, 63);
  text-shadow: none;
}
.logo:hover .cls2{
  fill: rgb(81, 224, 224);
  text-shadow: none;
}
.logo svg {
  height: 110px;
  width: 110px;
}
.cls1,
.cls2{
  transition: all 1s;
  text-shadow: -1px -1px 10px rgb(221, 63, 63), 0px 1px 10px rgb(81, 224, 224);
}
.cls1 {
  fill: rgba(221, 63, 63, 0.73);
  font-size: 76px;
  font-family: NiagaraEngraved-Reg, Niagara Engraved;
}
.cls2 {
  fill: rgba(81, 224, 224, 0.73);
  font-size: 59px;
  font-family: Stencil, Stencil;
}
