.video {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  max-height: 100vh;
  background-color: rgba(0, 0, 0, 0.1);
  position: relative;
}
.content {
  box-shadow: inset 10px -10px 20px 10px #000;
  padding: 10px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.418);
  display: flex;
  justify-content: center;
}
