.profile {
  height: 100vh;
  overflow-x: hidden;
  border-radius: 20px;
  box-shadow: inset -15px 0 60px 30px #000;
  padding: 4% 0;
  color: #fff;
}
.content {
  width: 70%;
  margin: 0 auto;
}
.title {
  display: flex;
}
.sidebar {
  width: 300px;
  height: 100%;
  margin-right: 10%;
}
.photo {
  width: inherit;
  height: 410px;
  border-radius: 15px;
  margin-bottom: 5%;
  overflow: hidden;
  transition: 0.5s ease-in-out;
  animation: border-color-hover 11s ease-in-out infinite forwards;
}
@keyframes border-color-hover {
  0% {
    box-shadow: 0px 0 3px 1px rgb(64, 21, 83);
  }
  20% {
    box-shadow: 0px 0 3px 1px rgb(235, 61, 61);
  }
  50% {
    box-shadow: 0px 0 3px 1px rgb(77, 214, 214);
  }
  65% {
    box-shadow: 0px 0 3px 1px rgb(64, 21, 83);
  }
  80% {
    box-shadow: 0px 0 13x 1px rgb(235, 61, 61);
  }
  100% {
    box-shadow: 0px 0 3px 1px rgb(64, 21, 83);
  }
}
.photo img {
  width: inherit;
  height: inherit;
  border-radius: inherit;
  transform: scale(1.9) translateY(10px);
}
.icons{
    padding: 1%;
    text-align: center;
}
.icons img{
    width: 35px;
    height: 35px;
    margin: 5px;
}
.header {
  font-family: "Lobster", cursive;
  font-size: 44px;
  margin-bottom: 5%;
}
.bodyTitle {
  font-family: "Rubik", sans-serif;
  font-size: 32px;
}
.bodyTitle span {
  margin-left: 1%;
  font-size: 18px;
  display: block;
  line-height: 18px;
}
