.navigation {
  width: 100%;
  text-align: center;
}
.icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90px;
  transition: 0.4s ease-in-out;
  transform-style: preserve-3d;
  perspective: 500px;
}
.icon:hover {
  box-shadow: inset -1px 0 10px 2px rgb(0, 0, 0);
}
.icon svg {
  width: 60px;
  height: 60px;
  fill: rgba(81, 224, 224, 0.719);
  transition: all 0.2s;
}
.icon:hover svg {
  transform: translateZ(-15px);
  fill: rgb(235, 61, 61);
}
.subtitle {
  font-family: "Roboto Mono", monospace;
  font-weight: 300;
  font-size: 1.1em;
  width: 100%;
}
.icon a.active {
  box-shadow: inset -1px 0 20px 4px rgb(0, 0, 0);
}
.icon a.active svg {
  transform: translateZ(-15px);
  fill: rgb(235, 61, 61);
}
