.author{
  margin-left: 20%;
  text-shadow: none;
  font-family: 'Rubik', sans-serif;
  font-size: 0.15em;
  color: rgb(209, 198, 198);
  line-height: 0;
  letter-spacing: 1px;
  transition: text-shadow 1.2s ease-in-out;
}
.author:hover{
  text-shadow: -1px -1px 0px rgb(81, 224, 224), 0px 1px 0px rgb(81, 224, 224);
}