.article{
  box-shadow: 1px 0 10px 5px #000;
  display: flex;
  border-radius: 20px;
  transition: all 0.2s ease-in-out;
  padding: 10px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.075);
  margin-bottom: 1%;
}
.article:hover{
  background-color: rgba(255, 255, 255, 0.199);
}
.article img{
  border-radius: 20px;
  width: 10%;
  height: 150px;
  margin-right: 20px;
}
.text {
  border-radius: 20px;
  padding: 10px;
  width: 100%;
}
.title {
  font-family: 'Baloo Thambi 2', cursive;
  font-size: 32px;
  padding-bottom: 10px;
}
.description {
  padding-left: 10px;
}
