/* red rgb(221, 63, 63) */
.contacts {
  width: 100%;
  min-height: 100vh;
  box-shadow: inset 0 0 60px 10px #000;
  display: flex;
  justify-content: center;
  align-items: center;
  transform-style: preserve-3d;
  perspective: 500px;
}
.plarform {
  position: relative;
  height: 65vh;
  width: 65vw;
  transform: translateZ(20px);
  background-color: inherit;
}
.plarform::before,
.plarform::after {
  content: "";
  position: absolute;
  border-radius: 10px;
  top: -2px;
  bottom: -2px;
  right: -2px;
  left: -2px;
  background: linear-gradient(
    45deg,
    #03fafa,
    #e75613,
    #03fafa,
    #ab0fda,
    #da1640,
    #16da16,
    #e75613,
    #03fafa,
    #e73ccb,
    #5355da,
    #16da16,
    #ee0909,
    #03fafa,
    #e75613,
    #03fafa,
    #ab0fda,
    #da1640,
    #16da16
  );
  background-size: 700%;
  z-index: -1;
  animation: animate-neon 70s ease-in-out infinite;
}
.plarform::after {
  filter: blur(5px);
}
@keyframes animate-neon {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 300% 0;
  }
  100% {
    background-position: 0 0;
  }
}
.content {
  height: inherit;
  width: inherit;
  padding: 25px;
  background-color: rgb(34, 34, 34);
  border-radius: 10px;
  box-shadow: 1px 0 10px 1px rgb(0, 0, 0);
  transform: translateZ(20px);
  position: relative;
  z-index: 10;
  display: flex;
}
.item {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.item + .item {
  margin-left: 5px;
}
.title {
  text-align: center;
  font-family: "Roboto Mono", monospace;
  font-weight: 500;
  font-size: 2.3em;
  text-shadow: -1px -1px 0px rgba(81, 224, 224, 0.616),
    0px 1px 0px rgba(81, 224, 224, 0.699);
  color: rgb(67, 172, 233);
  color: #26cdf7;
}
.icons {
  height: 100%;
  width: 100%;
  border-radius: 20px;
  padding: 40px 20px;
}
.icons a {
  margin: 10px;
  width: 120px;
  display: inline-block;
  padding: 10px;
  transition: all 0.4s ease-in-out;
}
.icons a:hover {
  border-radius: 20px;
  box-shadow: inset 0 0 30px 2px #000;
}
.icons img {
  transition: all 0.4s ease-in-out;
}
.icons a:hover img {
  transform: scale(0.82);
}
.inner {
  width: 80%;
  height: 50%;
  margin: 0 auto;
}
