.title {
  user-select: none;
  cursor: default;
  margin-top: 5%;
  font-family: "Arvo", serif;
  font-size: 15em;
  text-shadow: -1px -1px 1px rgb(221, 63, 63), 0px 1px 1px rgb(81, 224, 224);
  color: #222;
}
.title span {
  display: inline-block;
  animation: glowTitleText 30s ease-in-out 10s infinite;
}
@keyframes glowTitleText {
  20% {
    text-shadow: -1px -1px 5px rgb(221, 63, 63), 0px 1px 5px rgb(81, 224, 224);
  }
  30% {
    text-shadow: -1px -1px 1px rgb(221, 63, 63), 0px 1px 1px rgb(81, 224, 224);
  }
}
.title span:hover {
  transform: rotateZ(2deg);
  transition: all .2s ease-in-out;
  text-shadow: -1px -1px 3px rgb(128, 40, 40), 0px 1px 3px rgb(54, 134, 134);
}